	module.exports = {"data":{"markdownRemark":{"html":"<h2>Open Source</h2>\n<p>Open source software is any type of software that is available to the public to see or downloaded for free. Anyone is capable of creating an open source project.</p>\n<p>Open source projects are legally made open through a creative license. They are changed, maintained or abandoned by their creators at any time. Many open source projects have contributors that volunteer to add and edit code.</p>\n<p>In short, Open source softwares are of <strong>lower cost</strong>, <strong>higher security</strong> and <strong>better quality</strong> when compared to propreitary software.</p>\n<p>One of the biggest Open Source projects is Linux.</p>\n<h4>More Information:</h4>\n<p><a href=\"https://opensource.org/\">Open Source Initiative</a>\n<a href=\"https://en.wikipedia.org/wiki/Open-source_model\">Wikipedia Open Source</a></p>","fields":{"slug":"/working-in-tech/open-source/"},"frontmatter":{"title":"Open Source"}}},"pathContext":{"slug":"/working-in-tech/open-source/"}}

